import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LightHttp } from './../../../light/light-out/light-out.module';

@IonicPage()
@Component({
  selector: 'page-message-list',
  templateUrl: 'message-list.html',
})
export class MessageListPage {

  ///标题名称
  titlename: string = "消息中心";

  ///数据的列表
  datalist: Array<any> = [];

  ///数据连接
  dataurl: string = "Message/GetMessageList";

  ///id的列信息
  idcol: string = "CODE";

  ///搜索的内容
  searchQuery: string = "";

  ///数据的参数信息
  dataParam: any = {"limit":10,"offset":0,"fiter":"true"};

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    lightHttp: LightHttp
  ) {

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MessageListPage');
  }

  /**
   * 判断文件类型，显示对应图片
   * 
   * @param {any} input 
   * @returns {*} 
   * @memberof NewDetailPage
   */
  public fileType(input): any {
    var out = "";
    switch (input) {
      case "1":
        out = "新闻";
        break;
      case "2":
        out = "公告";
        break;
      case "3":
        out = "内部邮件";
        break;
      case "4":
        out = "待办流程";
        break;
      case "5":
        out = "日程提醒";
        break;
      default:
        out = "消息"
        break;
    }
    return out;
  }


}





